/* src/Navbar.css */
.navbar {
  position: fixed;
  top: 70px;
  left: 0;
  width: 100%;
  background-color: #333333e1;
  color: rgb(209, 209, 209);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease-in-out;
  transform: translateY(-100%);
  z-index: 10;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar ul {
  position: relative;
  list-style: none;
  display: flex;
  gap: 172px; /* المسافة بين العناصر */
}

.navbar li {
  position: relative; /* لجعل ::after يتفاعل بشكل صحيح */
  cursor: pointer;
  font-size: 25px;
  font-family: "Amiri", serif;
  font-style: normal;
  font-weight: bolder;
  padding: 10px 0; /* إضافة مسافة حول النص */
  transition: transform 0.3s ease, color 0.3s ease; /* تأثيرات الحركة */
}

.navbar li:hover {
  /* تكبير وتدوير عند المرور */
  color: #ffffff; /* تغيير لون النص عند المرور */
}

.navbar li::after {
  content: "";
  position: absolute;
  width: 100%; /* عرض كامل */
  height: 3px; /* ارتفاع الخط */
  background: linear-gradient(
    90deg,
    #ffffff,
    transparent,
    transparent,
    #00d0ec
  ); /* تأثير متقطع */
  bottom: 0; /* الموضع في الأسفل */
  left: 0;
  transform: translateX(-100%); /* بدء خارج الشاشة */
  transition: transform 0.5s ease; /* تأثير الانتقال */
}

.navbar li:hover::after,
.navbar li:focus::after {
  transform: translateX(0); /* خط الموجة يظهر عند المرور */
}
