.main-container {
  margin-top: 89px;
  width: 100%;
  height: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #b4cde6;
  justify-content: center;
}

.controls {
  margin-top: 100px;

  display: flex;
  justify-content: center;
}

.controls label {
  color: #999;
  display: inline-block;
  padding: 10px 20px;
  border: 1px solid #ccc;
  cursor: pointer;
  background: #252525;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.9);
  transition: transform 0.3s ease, background-color 0.3s ease;
  border-radius: 5px; /* لجعل الحواف دائرية */
  margin: 0 10px; /* مسافة بين العناصر */
}

.controls label:hover {
  background-color: #444; /* لون الخلفية عند التحويم */
}

.controls label:active {
  transform: scale(1.1); /* تكبير عند الضغط */
  background-color: #333; /* لون الخلفية عند الضغط */
  transition: transform 0.1s ease; /* تأثير أسرع عند الضغط */
}

.controls label:focus {
  outline: none; /* إزالة الإطار الافتراضي عند التركيز */
}

.coverflow-container {
  box-reflect: below 0px -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 60%, rgba(
          0,
          0,
          0,
          0.1
        )
        100%);
}

label {
  color: #999;
  display: inline-block;

  cursor: pointer;
  background: #252525;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.9);
  transition: color 0.3s ease;
}

label:hover {
  color: #fff;
}

.coverflow-list {
  width: 100%;
  display: flex;
  justify-content: center;
  perspective: 900px;
}
.box {
  transition: all 0.4s ease-in-out 0s; /* Applies to all properties */
}

.button {
  transition: background-color 0.5s ease-in; /* Applies only to background-color */
}

.box:hover {
  transform: scale(1.1);
  background-color: blue;
}

.coverflow-item {
  display: inline-block;
  background: #414141;
  margin: 0 -30px;
  position: relative;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.9);
  transition: 0.4s ease;
}

.album-cover {
  display: block;
  height: 150px;
  margin: 0;
}

.album-cover img {
  height: 250px;
  width: 250px;
  opacity: 0.5;
  transition: 1s ease-in-out;
}

.album-name {
  text-align: center;
  display: block;
  color: #628e90;
}

.coverflow-item {
  transform: rotateY(45deg);
  transition: 0.5s;
}

input[type="radio"] {
  display: none;
  transition: 0.5s;
}

input[type="radio"]:checked + .coverflow-item {
  transform: rotateY(0deg);
  margin: 0 auto;
  background: #313131;
  z-index: 10;
  transition: 0.5s;

  .album-cover img {
    opacity: 1;
    height: 350px;
    width: 350px;
  }

  .album-name {
    color: #ffffff;
  }

  ~ .coverflow-item {
    transform: rotateY(-45deg);
    transition: 0.5s;

    background: #515151;
  }
}
